@use '../../utilities' as u;

/*スクロールダウン全体の場所*/
.el_scrolldown_wrapper {
  position: relative;
}
.el_scrolldown {
  /*描画位置※位置は適宜調整してください*/
  position:absolute;
  left:50%;
  bottom:10px;
  /*全体の高さ*/
  height:50px;
}

/*Scrollテキストの描写*/
.el_scrolldown span {
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  font-size: u.rem(12);
  letter-spacing: 0.05em;
}

/* 線の描写 */
.el_scrolldown::after {
  content: "";
  position: absolute;
  top: 0;
  width: 1px;
  height: 30px;
  background: #fff;
    /*線の動き1.4秒かけて動く。永遠にループ*/
  animation: pathmove 1.4s ease-in-out infinite;
  opacity:0;
}

/*高さ・位置・透過が変化して線が上から下に動く*/
@keyframes pathmove {
  0% {
    height:0;
    top:0;
    opacity: 0;
  }
  30% {
    height:30px;
    opacity: 1;
  }
  100% {
    height:0;
    top:50px;
    opacity: 0;
  }
}